import React from 'react'
import {Trade, TradeType} from '@udegoswap/sdk'
import {Text} from '@pancakeswap/uikit'
import {Card, CardContent, CardHeader, Grid} from "@material-ui/core";
import {Field} from '../../../state/swap/actions'

import {useUserSlippageTolerance} from '../../../state/user/hooks'
import {computeSlippageAdjustedAmounts, computeTradePriceBreakdown} from '../../../utils/prices'
import {AutoColumn} from '../../../components/Layout/Column'
import QuestionHelper from '../../../components/QuestionHelper'
import {RowBetween, RowFixed} from '../../../components/Layout/Row'
import FormattedPriceImpact from './FormattedPriceImpact'
import SwapRoute from './SwapRoute'

function TradeSummary({trade, allowedSlippage, showRoute}) {

    const {priceImpactWithoutFee, realizedLPFee} = computeTradePriceBreakdown(trade)
    const isExactIn = trade.tradeType === TradeType.EXACT_INPUT
    const slippageAdjustedAmounts = computeSlippageAdjustedAmounts(trade, allowedSlippage)

    return (
        <div
            className="p-6 border-gray-100 dark:border-gray-300 shadow-xl hover:shadow-2xl bg-white bg-opacity-50 dark:bg-opacity-5 rounded-3xl transition-all">
            <div className="flex flex-row items-center justify-between mb-3 border-b border-gray-100 pb-5">
                <div className="flex flex-row items-center">
                    <h5 className="text-xl font-bold text-black dark:text-white items-start">Swap Details</h5>
                </div>
            </div>
            <div className="py-3">
                <Grid container spacing={1}>
                    <Grid item xs={6} md={6}>
                        Min. Received
                    </Grid>
                    <Grid item xs={6} md={6}>
                        <b>
                            {isExactIn
                                ? `${slippageAdjustedAmounts[Field.OUTPUT]?.toSignificant(4)} ${trade.outputAmount.currency.symbol}` ??
                                '-'
                                : `${slippageAdjustedAmounts[Field.INPUT]?.toSignificant(4)} ${trade.inputAmount.currency.symbol}` ?? '-'}
                        </b>
                    </Grid>
                </Grid>
                <Grid container spacing={1}>
                    <Grid item xs={6} md={6}>
                        Price Impact
                    </Grid>
                    <Grid item xs={6} md={6}>
                        <b style={{color: 'green'}}>
                            <FormattedPriceImpact priceImpact={priceImpactWithoutFee}/>
                        </b>
                    </Grid>
                </Grid>
                <Grid container spacing={1}>
                    <Grid item xs={6} md={6}>
                        Is Routed?
                    </Grid>
                    <Grid item xs={6} md={6}>
                        {
                            !showRoute &&
                            <b style={{color: 'red'}}>
                                False
                            </b>
                        }
                        {
                            showRoute &&
                            <SwapRoute trade={trade}/>
                        }
                    </Grid>
                </Grid>
                <Grid container spacing={1}>
                    <Grid item xs={6} md={6}>
                        Liquidity Fee
                    </Grid>
                    <Grid item xs={6} md={6}>
                        <b>{realizedLPFee ? `${realizedLPFee.toSignificant(4)} ${trade.inputAmount.currency.symbol}` : '-'}</b>
                    </Grid>
                </Grid>
            </div>
        </div>
)
}

export function AdvancedSwapDetails({trade}) {

    const [allowedSlippage] = useUserSlippageTolerance()

    const showRoute = Boolean(trade && trade.route.path.length > 2)

    return (
        <>
            {
                trade && (
                    <>
                        <TradeSummary trade={trade} allowedSlippage={allowedSlippage} showRoute={showRoute}/>
                    </>
                )}
        </>
    )
}
