import React from 'react';
import { Text, ErrorIcon } from '@pancakeswap/uikit';
import styled, { css } from 'styled-components';
import { AutoColumn } from '../../../components/Layout/Column';

export const Wrapper = styled.div `
  position: relative;
  padding: 1rem;
`;
export const ArrowWrapper = styled.div `
  padding: 2px;

  ${({ clickable }) => clickable
    ? css `
          :hover {
            cursor: pointer;
            opacity: 0.8;
          }
        `
    : null}
`;
export const ErrorText = styled(Text) `
  color: ${({ theme, severity }) =>
  // eslint-disable-next-line no-nested-ternary  
    severity === 3 || severity === 4
    ? theme.colors?.failure
    // eslint-disable-next-line no-nested-ternary
    : severity === 2
        ? theme.colors?.warning
        : severity === 1
            ? theme.colors?.text
            : theme.colors?.success};
    font-weight: bolder
`;
export const StyledBalanceMaxMini = styled.button `
  height: 22px;
  width: 22px;
  background-color: ${({ theme }) => theme.colors?.background};
  border: none;
  border-radius: 50%;
  padding: 0.2rem;
  font-size: 0.875rem;
  font-weight: 400;
  margin-left: 0.4rem;
  cursor: pointer;
  color: ${({ theme }) => theme.colors?.text};
  display: flex;
  justify-content: center;
  align-items: center;
  float: right;

  :hover {
    background-color: ${({ theme }) => theme.colors?.dropdown};
  }
  :focus {
    background-color: ${({ theme }) => theme.colors?.dropdown};
    outline: none;
  }
`;
export const TruncatedText = styled(Text).attrs({ ellipsis: true }) `
  width: 220px;
`;
const SwapCallbackErrorInner = styled.div `
  background-color: ${({ theme }) => `${theme.colors?.failure}33`};
  border-radius: 1rem;
  display: flex;
  align-items: center;
  font-size: 0.825rem;
  width: 100%;
  padding: 3rem 1.25rem 1rem 1rem;
  margin-top: -2rem;
  color: ${({ theme }) => theme.colors?.failure};
  z-index: -1;
  p {
    padding: 0;
    margin: 0;
    font-weight: 500;
  }
`;
const SwapCallbackErrorInnerAlertTriangle = styled.div `
  background-color: ${({ theme }) => `${theme.colors?.failure}33`};
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 12px;
  border-radius: 12px;
  min-width: 48px;
  height: 48px;
`;
export function SwapCallbackError({ error }) {
    return (React.createElement(SwapCallbackErrorInner, null,
        React.createElement(SwapCallbackErrorInnerAlertTriangle, null,
            React.createElement(ErrorIcon, { width: "24px" })),
        React.createElement("p", null, error)));
}
export const SwapShowAcceptChanges = styled(AutoColumn) `
  background-color: ${({ theme }) => `${theme.colors?.warning}33`};
  padding: 0.5rem;
  border-radius: 12px;
  margin-top: 8px;
`;