import { useEffect, useState } from 'react';
import { ethers } from 'ethers';
import BigNumber from 'bignumber.js';
import { useWeb3React } from '@web3-react/core';
import {ChainId} from "@udegoswap/sdk";
import { getBep20Contract, getUDEContract } from '../utils/contractHelpers';
import { BIG_ZERO } from '../utils/bigNumber';
import { simpleRpcProvider } from '../utils/providers';
import tokens from "../config/tokens";
import useRefresh from './useRefresh';
import useLastUpdated from './useLastUpdated';
import {useUDEBusdPrice} from "./useBUSDPrice";
import {useUDE} from "./useContract";
import {farmsConfig} from "../config/constants/index";
import {FARMING} from "../config/constants";
import {formatBigNumberToFixed} from "../utils/formatBalance";
import useActiveWeb3React from "./useWeb3";
import {getContract, getLPPrice} from "../config/helpers";

// eslint-disable-next-line import/no-mutable-exports,no-var
export const FetchStatus = {
    NOT_FETCHED: "not-fetched",
    SUCCESS: "success",
    FAILED: "failed"
}

const useTokenBalance = (tokenAddress) => {
    const { NOT_FETCHED, SUCCESS, FAILED } = FetchStatus;
    const [balanceState, setBalanceState] = useState({
        balance: BIG_ZERO,
        fetchStatus: NOT_FETCHED,
    });
    const { account } = useWeb3React();
    const { fastRefresh } = useRefresh();
    useEffect(() => {
        const fetchBalance = async () => {
            const contract = getBep20Contract(tokenAddress);
            try {
                const res = await contract.balanceOf(account);
                setBalanceState({ balance: new BigNumber(res.toString()), fetchStatus: SUCCESS });
            }
            catch (e) {
                console.error(e);
                setBalanceState((prev) => ({...prev, fetchStatus: FAILED}));
            }
        };
        if (account) {
            fetchBalance();
        }
    }, [account, tokenAddress, fastRefresh, SUCCESS, FAILED]);
    return balanceState;
};
export const useTotalSupply = () => {
    const { slowRefresh } = useRefresh();
    const [totalSupply, setTotalSupply] = useState();
    useEffect(() => {
        async function fetchTotalSupply() {
            const udeContract = getUDEContract();
            const supply = await udeContract.totalSupply();
            setTotalSupply(new BigNumber(supply.toString()));
        }
        fetchTotalSupply();
    }, [slowRefresh]);
    return totalSupply;
};
export const useBurnedBalance = (tokenAddress) => {
    const [balance, setBalance] = useState(BIG_ZERO);
    const { slowRefresh } = useRefresh();
    useEffect(() => {
        const fetchBalance = async () => {
            const contract = getBep20Contract(tokenAddress);
            const res = await contract.balanceOf('0x000000000000000000000000000000000000dEaD');
            setBalance(new BigNumber(res.toString()));
        };
        fetchBalance();
    }, [tokenAddress, slowRefresh]);
    return balance;
};
export const useGetBnbBalance = () => {
    const [fetchStatus, setFetchStatus] = useState(FetchStatus.NOT_FETCHED);
    const [balance, setBalance] = useState(ethers.BigNumber.from(0));
    const { account } = useWeb3React();
    const { lastUpdated, setLastUpdated } = useLastUpdated();
    useEffect(() => {
        const fetchBalance = async () => {
            try {
                const walletBalance = await simpleRpcProvider.getBalance(account);
                setBalance(walletBalance);
                setFetchStatus(FetchStatus.SUCCESS);
            }
            catch (_a) {
                setFetchStatus(FetchStatus.FAILED);
            }
        };
        if (account) {
            fetchBalance();
        }
    }, [account, lastUpdated, setBalance, setFetchStatus]);
    return { balance, fetchStatus, refresh: setLastUpdated };
};
export const useGetUdeBalance = () => {
    const { balance, fetchStatus } = useTokenBalance(tokens.mlx.address);
    // TODO: Remove ethers conversion once useTokenBalance is converted to ethers.BigNumber
    return { balance: ethers.BigNumber.from(balance.toString()), fetchStatus };
};

export const useLockedLiquidity = (liquid=0) => {
    const {library} = useActiveWeb3React()
    const udePrice = useUDEBusdPrice()
    const ude = useUDE()
    const [liquidity, setLiquidity] = useState(0)
    useEffect(() => {
        const getLiquidity = async () => {
            try{
                const balance = await ude.balanceOf(FARMING)
                const totalStaked = parseFloat(udePrice?.toFixed(8)) * formatBigNumberToFixed(balance)
                liquid += (liquidity+totalStaked)
                setLiquidity(liquid)
            }catch (e) {
                // console.log(e)
            }
        }
        getLiquidity()
    }, [ude, udePrice, setLiquidity])
    return liquidity
}

export default useTokenBalance;