import React, { useEffect, useState } from "react";
import {useTotalSupply} from "../../hooks/useTokenBalance";
import {useUDEBusdPrice} from "../../hooks/useBUSDPrice";
import {getBalanceNumber} from "../../utils/formatBalance";
import {fNumber} from "../../utils/formatNumber";


export default function MTPLTVL() {
    const totalSupply = useTotalSupply()
    const mlxRate = useUDEBusdPrice()

    const [ totalMarketCap, setMarketCap ] = useState(0)

    useEffect(() => {
        let totalMarketCapInst = parseFloat(mlxRate?.toFixed(18)) * parseFloat(totalSupply?.toFixed(5))
        setMarketCap(totalMarketCapInst)
    }, [ mlxRate, totalSupply, setMarketCap ])

    return (
        <>
            <div className="col-span-1 md:col-span-1">
                <div className="px-6 py-6 bg-white dark:bg-opacity-5 rounded-3xl shadow-2xl dark:shadow-xl hover:shadow-3xl transition-all">
                    <div className="flex flex-col xl:flex-row justify-start items-start xl:space-x-4">
                        <div className="w-full md:w-auto flex items-center md:justify-end mb-3 md:mb-0">
                            <img className="animate-pulse mx-auto h-24" src="https://thecrypto360.com/valuelocked.png" alt="" />
                        </div>
                        <div className="w-full md:w-auto md:pt-6 mb-3 md:mb-0">
                            <h3 className="mb-2 text-2xl font-bold text-white text-center md:text-left whitespace-pre">
                                <span className="dark:text-white text-red-700 whitespace-pre">
                                    {
                                        `$ ${fNumber(getBalanceNumber(totalMarketCap))}`
                                    }
                                </span>
                            </h3>
                            <p className="mb-0 text-sm font-medium text-black dark:text-indigo-100 text-center md:text-left">Total Market Cap</p>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}