import React, {useEffect} from "react";
// importing Link from react-router-dom to navigate to
// different end points.
import { Link } from "react-router-dom";
import Sidebar from "../Sidebar";
import TradingViewWidget, { Themes } from 'react-tradingview-widget';
import {Helmet} from "react-helmet";
import ConnectWallet from "../components/mtpl/ConnectWallet";
import useActiveWeb3React from "../hooks/useWeb3";
import SwapMTPL from "./swap-mtpl";
import {useTotalSupply} from "../hooks/useTokenBalance";
import {useDispatch, useSelector} from "react-redux";
import {getTotalSupply} from "../redux/slices/ude";
import {useUDE} from "../hooks/useContract";
import {fBigNumber, fNumber} from "../utils/formatNumber";
import {shortAddress} from "../utils/pageHelpers";
import MTPLProfile from "./MTPLProfile";
import MTPLTVL from "./home-mtpl/MTPLTVL";


const MTPLHome = () => {
  const [showModal, setShowModal] = React.useState(false);
  const [fromSelectModal, setfromSelectModal] = React.useState(false);
  const { account } = useActiveWeb3React()
  const mlx = useUDE()

  const totalSupply = useSelector((state) => state.ude.totalSupply);

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(getTotalSupply(mlx))
  }, [dispatch, mlx])

  return (
    <>
    {/* <Sidebar/> */}
    <div className='mx-auto lg:ml-64'>
        <div className='hero-section px-5 py-4 md:px-10 md:py-8 h-full relative'>
          <nav className="flex justify-between mb-4">
            <h2 className="title text-black dark:text-white text-opacity-75 text-3xl font-bold">Exchange</h2>
            {
              !account && <ConnectWallet />
            }
            {
              account &&
              <MTPLProfile />
            }
          </nav>
          <div className="w-full">
            <section className>
              <div className="mx-auto">
                <div className="grid grid-cols-1 md:grid-cols-3 gap-5 md:gap-8 mb-8">
                  <MTPLTVL />
                  <div className="col-span-1 md:col-span-1">
                    <div className="px-6 py-6 bg-white dark:bg-opacity-5 rounded-3xl shadow-2xl dark:shadow-xl hover:shadow-3xl transition-all">
                      <div className="flex flex-col xl:flex-row justify-start items-start xl:space-x-4">
                        <div className="w-full md:w-auto flex items-center md:justify-end mb-3 md:mb-0">
                          <img className="animate-pulse mx-auto h-24" src="images/emission.png" alt="" />
                        </div>
                        <div className="w-full md:w-auto md:pt-6 mb-3 md:mb-0">
                          <h3 className="mb-2 text-2xl font-bold text-white text-center md:text-left whitespace-pre">
                            <span className="dark:text-white text-orange-700 whitespace-pre">1 MLX</span>
                          </h3>
                          <p className="mb-0 text-sm font-medium text-black dark:text-indigo-100 text-center md:text-left">Emission Per Block</p>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="col-span-1 md:col-span-1">
                    <div className="px-6 py-6 bg-white dark:bg-opacity-5 rounded-3xl shadow-2xl dark:shadow-xl hover:shadow-3xl transition-all">
                      <div className="flex flex-col xl:flex-row justify-start items-start xl:space-x-4">
                        <div className="w-full md:w-auto flex items-center md:justify-end mb-3 md:mb-0">
                          <img className="animate-pulse mx-auto h-24" src="images/supply.png" alt="" />
                        </div>
                        <div className="w-full md:w-auto md:pt-6 mb-3 md:mb-0">
                          <h3 className="mb-2 text-2xl font-bold text-white text-center md:text-left whitespace-pre">
                            <span className="dark:text-white text-green-700 whitespace-pre">
                              {fNumber(fBigNumber(totalSupply))} MLX
                            </span>
                          </h3>
                          <p className="mb-0 text-sm font-medium text-black dark:text-indigo-100 text-center md:text-left">Total Supply</p>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </section>

            <section className="seed-sale-section py-10 flex flex-col items-center justify-between space-y-4 mb-5 hidden">
                <h3 className="text-2xl md:text-4xl text-white">Seed Sale is Live Now!</h3>
                <a href="https://seed.metaple.finance/" className="px-5 py-3 inline-block text-sm text-center text-white bg-gradient-to-r from-sky-700 to-blue-700 rounded-lg font-medium hover:from-pink-500 hover:to-pink-600 transition-all duration-700" target="_blank">Byu Now</a>
            </section>

            {/* Old Class : grid grid-cols-1 md:grid-cols-10 gap-8 */}
            <div className="w-full flex items-center justify-center md:justify-center">
              <div className="chart-box col-span-1 md:col-span-6 hidden">
                <div className="border border-gray-200 bg-white bg-opacity-5 w-100 h-full rounded-3xl">
                  <div className="overflow-hidden rounded-3xl">
                    {/* <TradingViewWidget
                      symbol="USDT:BNB"
                      theme={Themes.DARK}
                      locale="fr"
                    /> */}
                  </div>
                </div>
              </div>
              <SwapMTPL />
            </div>
          </div>
        </div>
    </div>
    </>
  );
};

export default MTPLHome;
