import { ChainId, currencyEquals, JSBI, Price } from '@udegoswap/sdk';
import { useMemo } from 'react';
import tokens, {mainnetTokens, testnetTokens} from '../config/tokens';
import useActiveWeb3React from "./useWeb3";
import { PairState, usePairs } from './usePairs';
import { wrappedCurrency } from '../utils/wrappedCurrency';
import { useUDEPairs } from './useUDEPairs';

const BUSD_MAINNET = mainnetTokens.busd;
const BUSD_TESTNET = testnetTokens.busd;
const { wbnb: WBNB } = tokens;
/**
 * Returns the price in BUSD of the input currency
 * @param currency currency to compute the BUSD price of
 */
export default function useBUSDPrice(currency) {
    const { chainId } = useActiveWeb3React();
    const wrapped = wrappedCurrency(currency, chainId);

    const tokenPairs = useMemo(() => [
        [chainId && wrapped && currencyEquals(WBNB, wrapped) ? undefined : currency, chainId ? WBNB : undefined],
        // eslint-disable-next-line no-void
        [(wrapped === null || wrapped === void 0 ? void 0 : wrapped.equals(BUSD_MAINNET)) ? undefined : wrapped, chainId === ChainId.MAINNET ? BUSD_MAINNET : BUSD_MAINNET],
        [chainId ? WBNB : undefined, chainId === ChainId.MAINNET ? BUSD_MAINNET : BUSD_MAINNET],
    ], [chainId, currency, wrapped]);
    const [[ethPairState, ethPair], [busdPairState, busdPair], [busdEthPairState, busdEthPair]] = useUDEPairs(tokenPairs);
    // console.log(tokenPairs)
    return useMemo(() => {
        if (!currency || !wrapped || !chainId) {
            return undefined;
        }
        // handle weth/eth
        if (wrapped.equals(WBNB)) {
            if (busdPair) {
                const price = busdPair.priceOf(WBNB);
                return new Price(currency, BUSD_MAINNET, price.denominator, price.numerator);
            }
            return undefined;
        }
        // handle busd
        if (wrapped.equals(BUSD_MAINNET)) {
            return new Price(BUSD_MAINNET, BUSD_MAINNET, '1', '1');
        }
        // eslint-disable-next-line no-void
        const ethPairETHAmount = ethPair === null || ethPair === void 0 ? void 0 : ethPair.reserveOf(WBNB);
        const ethPairETHBUSDValue = ethPairETHAmount && busdEthPair ? busdEthPair.priceOf(WBNB).quote(ethPairETHAmount).raw : JSBI.BigInt(0);
        // all other tokens
        // first try the busd pair
        if (busdPairState === PairState.EXISTS &&
            busdPair &&
            busdPair.reserveOf(BUSD_MAINNET).greaterThan(ethPairETHBUSDValue)) {
            const price = busdPair.priceOf(wrapped);
            return new Price(currency, BUSD_MAINNET, price.denominator, price.numerator);
        }
        if (ethPairState === PairState.EXISTS && ethPair && busdEthPairState === PairState.EXISTS && busdEthPair) {
            if (busdEthPair.reserveOf(BUSD_MAINNET).greaterThan('0') && ethPair.reserveOf(WBNB).greaterThan('0')) {
                const ethBusdPrice = busdEthPair.priceOf(BUSD_MAINNET);
                const currencyEthPrice = ethPair.priceOf(WBNB);
                const busdPrice = ethBusdPrice.multiply(currencyEthPrice).invert();
                return new Price(currency, BUSD_MAINNET, busdPrice.denominator, busdPrice.numerator);
            }
        }
        return undefined;
    }, [chainId, currency, ethPair, ethPairState, busdEthPair, busdEthPairState, busdPair, busdPairState, wrapped]);
}
export const useUDEBusdPrice = () => {
    const udeBusdPrice = useBUSDPrice(tokens.mlx);
    // console.log(udeBusdPrice)
    return udeBusdPrice;
};
export const useBNBBusdPrice = () => {
    const bnbBusdPrice = useBUSDPrice(tokens.wbnb);
    return bnbBusdPrice;
};