import React from "react";
import {useCallback, useMemo, useState, useEffect} from "react";
import {useNavigate} from "react-router-dom";
import {JSBI, TradeType} from "@udegoswap/sdk";
import { Token} from "@udegoswap/sdk";
import useActiveWeb3React from "../../hooks/useWeb3";

import {
    useDefaultsFromURLSearch,
    useDerivedSwapInfo,
    useSwapActionHandlers,
    useSwapState
} from "../../state/swap/hooks";
import {useAllTokens, useCurrency} from "../../hooks/Tokens";
import {useExpertModeManager, useUserSingleHopOnly, useUserSlippageTolerance} from "../../state/user/hooks";
import useWrapCallback, {WrapType} from "../../hooks/useWrapCallback";
import { Field } from '../../state/swap/actions'
import {ApprovalState, useApproveCallbackFromTrade} from "../../hooks/useApproveCallback";
import {useIsTransactionUnsupported} from "../../hooks/Trades";
import {maxAmountSpend} from "../../utils/maxAmountSpend";
import {computeSlippageAdjustedAmounts, computeTradePriceBreakdown, warningSeverity} from "../../utils/prices";
import confirmPriceImpactWithoutFee from "./components/confirmPriceImpactWithoutFee";
import {useSwapCallback} from "../../hooks/useSwapCallback";
import {useCurrencyBalance} from "../../state/wallet/hooks";

import useToast from "../../hooks/useToast";
import ConnectWallet from "../../components/mtpl/ConnectWallet";
import TokenSelect from "./TokenSelect";
import CircleLoader from "../../components/Loader/CircleLoader";
import TradePrice from "./components/TradePrice";
import AdvancedSwapDetailsDropdown from "./components/AdvancedSwapDetailsDropdown";
import {Grid} from "@material-ui/core";

export default function SwapMTPL () {

    const { account } = useActiveWeb3React()
    const navigate = useNavigate()
    const loadedURLParams = useDefaultsFromURLSearch()

    const { toastSuccess } = useToast()

    const [loadedInputCurrency, loadedOutputCurrency] = [
        useCurrency(loadedURLParams?.inputCurrencyId),
        useCurrency(loadedURLParams?.outputCurrencyId)
    ]

    const urlLoadedTokens = useMemo(
        () => [loadedInputCurrency, loadedOutputCurrency]?.filter((c) => c instanceof Token) ?? [],
        [loadedInputCurrency, loadedOutputCurrency],
    )

    const defaultTokens = useAllTokens()
    const [allowedSlippage] = useUserSlippageTolerance()

    const [isExpertMode] = useExpertModeManager()

    const { independentField, typedValue, recipient } = useSwapState()
    const { v2Trade, currencyBalances, parsedAmount, currencies, inputError: swapInputError } = useDerivedSwapInfo()

    const { wrapType, execute: onWrap, inputError: wrapInputError, } = useWrapCallback(currencies[Field.INPUT], currencies[Field.OUTPUT], typedValue)
    const showWrap = wrapType !== WrapType.NOT_APPLICABLE
    const trade = showWrap ? undefined : v2Trade

    const parsedAmounts = showWrap ? { [Field.INPUT]: parsedAmount, [Field.OUTPUT]: parsedAmount } : {
        [Field.INPUT]: independentField === Field.INPUT ? parsedAmount : trade?.inputAmount,
        [Field.OUTPUT]: independentField === Field.OUTPUT ? parsedAmount : trade?.outputAmount,
    }

    const { onSwitchTokens, onCurrencySelection, onUserInput, onChangeRecipient } = useSwapActionHandlers()
    const isValid = !swapInputError
    const dependentField = independentField === Field.INPUT ? Field.OUTPUT : Field.INPUT

    const handleTypeInput = useCallback((value) => {
            onUserInput(Field.INPUT, value)
        },
        [onUserInput]
    )
    const handleTypeOutput = useCallback((value) => {
            onUserInput(Field.OUTPUT, value)
        },
        [onUserInput],
    )

    const [{ tradeToConfirm, swapErrorMessage, attemptingTxn, txHash }, setSwapState] = useState({
        tradeToConfirm: undefined,
        attemptingTxn: false,
        swapErrorMessage: undefined,
        txHash: undefined,
    })

    const formattedAmounts = {
        [independentField]: typedValue,
        [dependentField]: showWrap
          ? parsedAmounts[independentField]?.toExact() ?? ''
          : parsedAmounts[dependentField]?.toSignificant(6) ?? '',
    }

    const route = trade?.route
    const userHasSpecifiedInputOutput = Boolean(
        currencies[Field.INPUT] && currencies[Field.OUTPUT] && parsedAmounts[independentField]?.greaterThan(JSBI.BigInt(0)),
    )
    const noRoute = !route
    // check whether the user has approved the router on the input token
    const [approval, approveCallback] = useApproveCallbackFromTrade(trade, allowedSlippage)

    // check if user has gone through approval process, used to show two step buttons, reset on token change
    const [approvalSubmitted, setApprovalSubmitted] = useState(false)

    useEffect(() => {
        if (approval === ApprovalState.PENDING) {
            setApprovalSubmitted(true);
        }
    }, [approval, approvalSubmitted]);
    const maxAmountInput = maxAmountSpend(currencyBalances[Field.INPUT]);
    let _f;
    // eslint-disable-next-line no-cond-assign
    const atMaxAmountInput = Boolean(maxAmountInput && ((_f = parsedAmounts[Field.INPUT]) === null || _f === undefined ? undefined : _f.equalTo(maxAmountInput)));
    // the callback to execute the swap
    const { callback: swapCallback, error: swapCallbackError } = useSwapCallback(trade, allowedSlippage, recipient);
    const { priceImpactWithoutFee } = computeTradePriceBreakdown(trade);

    const handleSwap = useCallback(() => {
        if (priceImpactWithoutFee && !confirmPriceImpactWithoutFee(priceImpactWithoutFee)) {
            return;
        }
        if (!swapCallback) {
            return;
        }
        setSwapState({ attemptingTxn: true, tradeToConfirm, swapErrorMessage: undefined, txHash: undefined });
        swapCallback()
            .then((hash) => {
            setSwapState({ attemptingTxn: false, tradeToConfirm, swapErrorMessage: undefined, txHash: hash });
            onUserInput(Field.INPUT, '')
        })
            .catch((error) => {
            setSwapState({
                attemptingTxn: false,
                tradeToConfirm,
                swapErrorMessage: error.message,
                txHash: undefined,
            });
        });
    }, [priceImpactWithoutFee, swapCallback, tradeToConfirm]);
    // errors
    const [showInverted, setShowInverted] = useState(false);
    // warnings on slippage
    const priceImpactSeverity = warningSeverity(priceImpactWithoutFee);
    // show approve flow when: no error on inputs, not approved or pending, or approved in current session
    // never show if price impact is above threshold in non expert mode
    const showApproveFlow = !swapInputError &&
        (approval === ApprovalState.NOT_APPROVED ||
            approval === ApprovalState.PENDING ||
            (approvalSubmitted && approval === ApprovalState.APPROVED)) &&
        !(priceImpactSeverity > 3 && !isExpertMode);

    const handleInputSelect = useCallback((inputCurrency) => {
        setApprovalSubmitted(false); // reset 2 step UI for approvals
        onCurrencySelection(Field.INPUT, inputCurrency);
    }, [onCurrencySelection]);
    const handleMaxInput = useCallback(() => {
        if (maxAmountInput) {
            onUserInput(Field.INPUT, maxAmountInput.toExact());
        }
    }, [maxAmountInput, onUserInput]);
    const handleOutputSelect = useCallback((outputCurrency) => {
        // // console.log(outputCurrency)
        onCurrencySelection(Field.OUTPUT, outputCurrency);
    }, [onCurrencySelection]);
    const swapIsUnsupported = useIsTransactionUnsupported(currencies === null || currencies === undefined ? undefined : currencies.INPUT, currencies === null || currencies === undefined ? undefined : currencies.OUTPUT);

    const openPool = () => {
        navigate('/pool')
    }

    const [isDialogOpen, setDialog] = useState(false);
    const selectDialog = () => {
        setDialog(true)
    }
    const handleClose = () => {
        setDialog(false)
    }

    // console.log(currencies[Field.OUTPUT])

    return (
        <>
            <div className="relative col-span-1 md:w-1/2 lg:w-1/3 justify-center mx-auto">
                <div className="p-6 border-gray-100 dark:border-gray-300 shadow-xl hover:shadow-2xl bg-white bg-opacity-50 dark:bg-opacity-5 rounded-3xl transition-all">
                  <div className="flex flex-row items-center justify-between mb-3 border-b border-gray-100 pb-5">
                    <div className="flex flex-row items-center">
                      <div className="chart-icon text-gray-500 dark:text-gray-400 focus:outline-none focus:ring-4 focus:ring-gray-200 dark:focus:ring-gray-700 text-sm px-3 py-2 border border-gray-300 cursor-pointer mr-2 transition-all"><i className="las la-exchange-alt text-xl" /></div>
                      <h5 className="text-xl font-bold text-black dark:text-white items-start">Swap</h5>
                    </div>
                    <button type="button" className="text-gray-500 dark:text-gray-400 focus:outline-none focus:ring-1 focus:ring-gray-200 dark:focus:ring-gray-200 dark:focus:ring-0 h-5 w-5" onClick={setDialog}>
                      <i className="las la-cog text-2xl text-gray-500 hover:animate-spin hover:text-pink-500" />
                    </button>

                    {isDialogOpen ? (
                        <>
                            <div className="justify-center items-center flex overflow-x-hidden overflow-y-auto fixed inset-0 z-50 outline-none focus:outline-none px-3">
                                <div className="relative w-auto my-6 mx-auto max-w-6xl lg:w-1/4">
                                    {/*content*/}
                                    <div className="border-0 rounded-3xl shadow-lg relative flex flex-col w-full bg-white dark:bg-indigo-900 outline-none focus:outline-none">
                                        {/*header*/}
                                        <div className="flex items-start justify-between p-5 border-b border-solid border-blueGray-200 dark:border-gray-100 dark:border-opacity-10 rounded-t">
                                            <h3 className="text-lg font-semibold">Settings</h3>
                                            <button className="p-1 ml-auto bg-transparent border-0 text-black opacity-40 float-right text-3xl leading-none font-semibold outline-none focus:outline-none" onClick={handleClose}>
                                                <span className="bg-transparent text-black dark:text-white hover:text-indigo-600 h-6 w-6 text-2xl block outline-none focus:outline-none"></span>
                                            </button>
                                        </div>
                                        {/*body*/}
                                        <div className="relative p-6 flex-auto">
                                            <div className="overflow-hidden">
                                                <div className="token-list">
                                                    <h3 className="text-md text-white">Slippage Tolerance</h3>
                                                    <div className="button-group fency-checkbox flex items-center space-x-3 mt-4">
                                                        <div className="btn relative">
                                                            <input type="radio" name="tolerance" className="absolute opacity-0" id="01"></input>
                                                            <label for="01" className="px-4 py-2 rounded-full bg-white bg-opacity-5 text-xs font-semibold text-white hover:bg-pink-500 hover:bg-opacity-20 block cursor-pointer tracking-wide focus:bg-opacity-90">0.1%</label>
                                                        </div>
                                                        <div className="btn relative">
                                                            <input type="radio" name="tolerance" className="absolute opacity-0" id="05"></input>
                                                            <label for="05" className="px-4 py-2 rounded-full bg-white bg-opacity-5 text-xs font-semibold text-white hover:bg-pink-500 hover:bg-opacity-20 block cursor-pointer tracking-wide">0.5%</label>
                                                        </div>
                                                        <div className="btn relative">
                                                            <input type="radio" name="tolerance" className="absolute opacity-0" id="10"></input>
                                                            <label for="10" className="px-4 py-2 rounded-full bg-white bg-opacity-5 text-xs font-semibold text-white hover:bg-pink-500 hover:bg-opacity-20 block cursor-pointer tracking-wide">1.0%</label>
                                                        </div>
                                                        <div className="btn relative flex items-center space-x-2">
                                                            <input type="text" name="tolerance" className="w-16 px-4 py-2 rounded-full bg-white bg-opacity-5 text-xs font-semibold text-white hover:bg-pink-500 hover:bg-opacity-20 block cursor-pointer tracking-wide focus:outline-0 focus:ring-0" id="custom"></input>
                                                            <label for="custom" className="text-xs font-semibold text-white">%</label>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        {/*footer*/}
                                        <div className="flex items-center flex-col justify-end px-6 py-4 border-t border-solid border-blueGray-200 dark:border-gray-100 dark:border-opacity-10 rounded-b">
                                            <button
                                                className="bg-indigo-500 hover:bg-sky-600 text-white active:bg-sky-600 font-bold uppercase text-xs w-full px-6 py-3 rounded-xl shadow hover:shadow-lg outline-none focus:outline-none ease-linear transition-all duration-150" type="button" onClick={handleClose}>Close</button>
                                            {/* <button className="bg-emerald-500 text-white active:bg-emerald-600 font-bold uppercase text-sm px-6 py-3 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150" type="button" onClick={() => setShowModal(false)}>Save Changes</button> */}
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="opacity-75 fixed inset-0 z-40 bg-black backdrop-blur-xl"></div>
                        </>
                    ) : null}

                  </div>
                  <div className="py-3">
                    <TokenSelect
                      placeHolder="Input Amount"
                      label="From"
                      isMax
                      tokenAddress={currencies[Field.INPUT]?.address}
                      tokenSymbol={currencies[Field.INPUT]?.symbol}
                      tokenImg={`./tokens/${currencies[Field.INPUT]?.symbol ?? 'bnb'}.png`}
                      onItemChange={handleInputSelect}
                      onInputChange={handleTypeInput}
                      inputValue={formattedAmounts[Field.INPUT]}
                      selectedToken={currencies[Field.INPUT]}
                      showApprove={showApproveFlow}
                      isDisabled={approval !== ApprovalState.NOT_APPROVED || approvalSubmitted}
                      onApprove={approveCallback}
                      onMax={handleMaxInput}
                      approveText={(showApproveFlow) &&
                        // eslint-disable-next-line no-nested-ternary
                        (approval === ApprovalState.PENDING ? (
                            <div gap="6px" justify="center">
                              Approving...
                            </div>
                        ) : approvalSubmitted && approval === ApprovalState.APPROVED ? (
                            'Approved'
                        ) : (
                            `Approve ${currencies[Field.INPUT]?.symbol ?? '' }`
                        ))}
                    />
                  </div>
                  <div className="separator flex align-middle justify-center">
                    <a
                        href="javascript:;"
                        className="shadow-2xl p-1"
                        onClick={() => {
                            // // console.log("[+] Switch Tokens")
                            setApprovalSubmitted(false) // reset 2 step UI for approvals
                            onSwitchTokens()
                        }}
                    >
                        <i className="las la-arrow-down text-blue-900" />
                    </a>
                  </div>
                  <div className="py-3">
                    <TokenSelect
                      placeHolder="Output Amount"
                      label="To (estimated)"
                      tokenAddress={currencies[Field.OUTPUT]?.address}
                      tokenSymbol={currencies[Field.OUTPUT]?.symbol}
                      tokenImg={`./tokens/${currencies[Field.OUTPUT]?.address}.png`}
                      onItemChange={handleOutputSelect}
                      onInputChange={handleTypeOutput}
                      selectedToken={currencies[Field.OUTPUT]}
                      inputValue={formattedAmounts[Field.OUTPUT]}
                      showApprove={false}
                    />
                  </div>
                    <div className="flex flex-row items-center justify-between mt-3 mb-3 border-b border-gray-100 pb-5 text-white text-sm font-semibold">
                        <TradePrice
                            price={trade?.executionPrice}
                            showInverted={showInverted}
                            setShowInverted={setShowInverted}
                        />
                    </div>
                  {
                    !account &&
                    <ConnectWallet />
                  }
                    {
                        showApproveFlow && (
                        <button
                            className="px-2 py-4 mt-4 text-center text-white bg-gradient-to-r from-sky-600 to-blue-700 rounded-lg w-full font-medium hover:from-sky-800 hover:to-blue-900 transition-all"
                            onClick={approveCallback}
                            disabled={approval !== ApprovalState.NOT_APPROVED || approvalSubmitted}
                        >
                            {
                                (approval === ApprovalState.PENDING ? (
                                <div gap="6px" justify="center">
                                    <CircleLoader stroke="white" />
                                </div>
                                ) : approvalSubmitted && approval === ApprovalState.APPROVED ? (
                                'Approved'
                                ) : (
                                `Approve ${currencies[Field.INPUT]?.symbol ?? '' }`
                                ))
                            }
                        </button>
                        )
                    }
                    {
                        (account) &&
                        <button
                            className="px-2 py-4 mt-4 text-center text-white bg-gradient-to-r from-sky-600 to-blue-700 rounded-lg w-full font-medium hover:from-sky-800 hover:to-blue-900 transition-all"
                            fullWidth
                            color={isValid && priceImpactSeverity > 2 ? 'error' : 'primary'}
                            sx={{maxWidth: 620, borderRadius: 25}}
                            onClick={() => {
                                handleSwap()
                            }}
                            disabled={
                                !isValid || approval !== ApprovalState.APPROVED || (priceImpactSeverity > 3 && !isExpertMode)
                            }
                        >
                            {
                                // eslint-disable-next-line no-nested-ternary
                                (
                                    (noRoute && userHasSpecifiedInputOutput) ? (
                                        'Insufficient liquidity for this trade'
                                    ) : (
                                        swapInputError ||
                                        (
                                            // eslint-disable-next-line no-nested-ternary
                                            approval !== ApprovalState.APPROVED ? 'Approve Token' :
                                                priceImpactSeverity > 3 && !isExpertMode
                                                ? 'Price Impact Too High'
                                                : priceImpactSeverity > 2
                                                ? 'Swap Anyway'
                                                : 'Swap'
                                        )
                                    )
                                )
                            }
                        </button>
                    }
                    {swapIsUnsupported && (
                        <button
                            className="px-2 py-4 mt-4 text-center text-white bg-gradient-to-r from-indigo-900 to-blue-900 rounded-lg w-full font-medium hover:from-blue-900 hover:to-indigo-900 transition-all"
                            width="100%"
                            variant="contained"
                            disabled
                            mb="4px"
                        >
                            Unsupported Asset
                        </button>
                    )
                    }
                    {/*<div className="flex flex-row items-center justify-between mt-3 mb-3 border-b border-gray-100 pb-5">*/}
                    {/*    {*/}
                    {/*        (!swapIsUnsupported && trade) && (*/}
                    {/*            <AdvancedSwapDetailsDropdown trade={trade} />*/}
                    {/*        )*/}
                    {/*    }*/}
                    {/*</div>*/}
                </div>
                <Grid item xs={12} md={12} sx={{display: 'flex', justifyContent:'center', alignItems: 'center'}}>
                    {
                        (!swapIsUnsupported && trade) && (
                            <AdvancedSwapDetailsDropdown trade={trade} />
                        )
                    }
                </Grid>
              </div>
        </>
    )
}